<template>
  <div class="">
      <page-title title='forminput'></page-title>
      <el-row :gutter="20">
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
            STANDARD FIELDS
            <hr>
            Text
            <el-input placeholder=""></el-input>
            With Icon
            <el-input placeholder=""  icon="search"></el-input>
            Disabled Input
            <el-input placeholder="" :disabled="true"></el-input>
            Textarea
            <el-input placeholder="" type="textarea" :rows="2"></el-input>
            Small Input
            <el-input placeholder="" size="small"></el-input>
            Large Input
            <el-input placeholder="" size="large"></el-input>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
            EXAMPLE FORM<hr>
            <el-form ref="form" label-width="80px" :model="form">
              <el-form-item label="">
                 <el-input></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-col  :span="12">
                   <el-date-picker type="date" v-model="form.date1" placeholder=""  style="width: 100%;"></el-date-picker>
                </el-col>
                <el-col :span="12">
                  <el-time-picker type="fixed-time" v-model="form.date2" placeholder=""  style="width: 100%;"></el-time-picker>
                </el-col>
              </el-form-item>
              <el-form-item label="">
                 <el-checkbox-group v-model="form.type">
                   <el-checkbox label="/" name="type"></el-checkbox>
                   <el-checkbox label="" name="type"></el-checkbox>
                 </el-checkbox-group>
              </el-form-item>
              <el-form-item>
                 <el-button type="primary" @click="onSubmit"></el-button>
                 <el-button></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
            Time PICKER<hr>
            <el-time-select style="width:100%; margin-bottom:20px" v-model="value1" placeholder=""  :picker-options="{start: '08:30',step: '00:15',end: '18:30'}">
            </el-time-select>
            <el-time-select
                placeholder=""
                style="width: 49%;"
                v-model="startTime"
                :picker-options="{
                start: '08:30',
                step: '00:15',
                end: '18:30'
                }">
            </el-time-select>
            <el-time-select
                placeholder=""
                v-model="endTime"
                style="width: 49%;"
                :picker-options="{
                start: '08:30',
                step: '00:15',
                end: '18:30',
                minTime: startTime
                }">
            </el-time-select><br/><br/>
          </el-card>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
             SELECTS <hr>
              
             <el-select v-model="value4"  placeholder="" style="width:100%">
               <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
             </el-select>
             
             <el-select v-model="value2"  placeholder="" style="width: 100%">
               <el-option
                  v-for="item in options1"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disabled">
                </el-option>
             </el-select>
             
             <el-select v-model="value2" disabled placeholder="" style="width: 100%">
               <el-option
                  v-for="item in options1"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disabled">
                </el-option>
             </el-select>
             
             <el-select v-model="value2" multiple  placeholder="" style="width: 100%">
               <el-option
                  v-for="item in options1"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disabled">
                </el-option>
             </el-select>
             
             <el-select v-model="value7" placeholder="" style="width:100%">
                <el-option-group
                  v-for="group in options3"
                  :key="group.label"
                  :label="group.label">
                    <el-option
                      v-for="item in group.options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-option-group>
              </el-select>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
             INPUT GROUPS <hr>
             <el-input placeholder="" v-model="input3">
                <template slot="prepend">Http://</template>
             </el-input>
             <el-input placeholder="" v-model="input4" style="margin-top:15px">
                <template slot="append">.com</template>
             </el-input>
             <el-input placeholder="" v-model="input5" style="margin-top:15px">
                <el-select v-model="select" slot="prepend" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                </el-select>
                <el-button slot="append" icon="search"></el-button>
              </el-input>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="24" :md="12" :lg="12">
          <el-card class="box-card">
             RATE <hr>
             
             <el-rate
                v-model="value8"
                show-text>
             </el-rate>
              
              <el-rate
                v-model="value5"
                disabled
                show-text
                text-color="#ff9900"
                text-template="{value}">
              </el-rate>
          </el-card>
        </el-col>
      </el-row>
  </div>
</template>

<script>
 import pageTitle from '../../components/common/pageTitle'
 export default{
   components: {pageTitle},
   data() {
     return {
       form: {
         date1: '',
         date2: '',
         type: []
       },
       value1: '',
       startTime: '',
       endTime: '',
       options: [{
           value: '1',
           label: ''
         }, {
           value: '2',
           label: ''
         }, {
           value: '3',
           label: ''
         }, {
           value: '4',
           label: ''
         }, {
           value: '5',
           label: ''
         }],
         options1: [{
             value: '1',
             label: ''
           }, {
             value: '2',
             label: '',
             disabled: true
           }, {
             value: '3',
             label: ''
           }, {
             value: '4',
             label: ''
           }, {
             value: '5',
             label: ''
           }],
           options3: [{
              label: '',
              options: [{
                value: 'Shanghai',
                label: ''
              }, {
                value: 'Beijing',
                label: ''
              }]
            }, {
              label: '',
              options: [{
                value: 'Chengdu',
                label: ''
              }, {
                value: 'Shenzhen',
                label: ''
              }, {
                value: 'Guangzhou',
                label: ''
              }, {
                value: 'Dalian',
                label: ''
              }]
            }],
         value4: "",
         value2: "",
         value5: 3.7,
         value7: "",
         value8: null,
         input3: '',
         input4: '',
         input5: '',
         select: ""
     }
   },
   methods: {
     onSubmit() {
       console.log(1222)
     }
   }
 }
</script>

<style scoped>
.el-col{
  margin-bottom:16px;
}
.material-icons{
  font-size:80px;
  color:#ddd;
}
.cart-string{
  height:100px;
  padding-top:10px;
  font-size:1.1rem;
}
.box-card{
  font-size:12px;
  line-height:26px;
}
.box-card hr{
  height:1px;
  border:none;
  border-top:1px  dashed #ccc;
  margin-bottom:10px;
}
.el-select .el-input {
  width: 140px;
 }
</style>



